import React, { Component } from "react";
import { connect } from "react-redux";
import { Field, reduxForm } from "redux-form";
import { getSupplier, showModal } from "../../../actions/datamaster_action";
import { getToday } from "../../../components/notification/function";
import {
  ReanderField,
  ReanderSelect,
} from "../../../components/notification/notification";

class HeadServiceLuar extends Component {
  constructor(props) {
    super(props);
    this.state = {};
  }

  componentDidMount() {
    this.props.dispatch(getSupplier());
    this.props.change("tanggal", getToday());
  }
  render() {
    return (
      <div className="row">
        <div className="col-lg-4">
          <Field
            name="no_service"
            component={ReanderField}
            type="text"
            label="Nomor Service"
            placeholder="Masukan Nomor Service"
            readOnly
          />
        </div>
        <div className="col-lg-4">
          <Field
            name="tanggal"
            component={ReanderField}
            type="date"
            label="Tanggal"
            placeholder="Masukan Tanggal"
          />
        </div>
        <div className="col-lg-4"></div>
        <div className="col-lg-4">
          <Field
            name="supplier"
            component={ReanderSelect}
            options={this.props.listsupplier.map((list) => {
              let data = {
                value: list.kode_supplier,
                name: list.nama_supplier,
              };
              return data;
            })}
            type="text"
            label="Supplier"
            placeholder="Masukan Supplier"
          />
        </div>
        <div className="col-lg-4">
          <Field
            name="alamat"
            component={ReanderField}
            type="text"
            label="Alamat"
            placeholder="Masukan Alamat"
            readOnly
          />
        </div>
        <div className="col-lg-4">
          <Field
            name="handphone"
            component={ReanderField}
            type="text"
            label="Handphone"
            placeholder="Masukan Handphone"
            readOnly
          />
        </div>
        <div className="col-lg-12">
          <div className="text-right">
            <button
              className="btn btn-primary"
              onClick={() => this.props.dispatch(showModal())}
            >
              Tambah Data <i className="fa fa-plus ml-3"></i>
            </button>
          </div>
        </div>
      </div>
    );
  }
}

HeadServiceLuar = reduxForm({
  form: "HeadServiceLuar",
  enableReinitialize: true,
})(HeadServiceLuar);
export default connect((state) => {
  return {
    listsupplier: state.datamaster.listsupplier,
  };
})(HeadServiceLuar);
